import Vue from "vue";
import router from "./router";
import App from "./App.vue";
import VueGtag from "vue-gtag";
import store from "./store";
import vuetify from "./plugins/vuetify";


Vue.config.productionTip = false;

Vue.use(VueGtag, {
  config: { id: "UA-160193864-1" }
}, router);

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App)
}).$mount("#app");
